import React, { Component } from 'react';
import { OverlayTrigger, Tooltip, Button } from 'react-bootstrap'; 
import { FontAwesomeIcon } from '../../../../../../../Assets/AssetsHelper';


class CodeShareCopy extends Component {
    constructor() {
        super();
        this.state = { showModal: false, codeCopied: false, isLoading: false, widthSign: "%", heightSign: "%", width: "100", height: "100", id: "#embedCode_" };
    }
    shareEmbedModal = (id) => event => this.setState({
        showModal: true,
        id: id
    })
    shareEmbedCallback = (shareEmbedEvent) => {
        this.setState({
            showModal: shareEmbedEvent
        })
    }

    codeCopy = (id) => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            })
        }, 1500);
        navigator.clipboard.writeText(this.props.copyText)
        // COPY_TO_CLIPBOARD(id);
    }
    render() {
        const { codeCopied } = this.state
        const { copyText, id, height, onClickToOpen, type, readOnly } = this.props

        return <div className="mb-2 mb-xl-3 position-relative link_uploader_textarea">
            <textarea className={`form-control textarea-min-row-${height} fs-9`} id={id} onClick={readOnly ? () => { this.codeCopy(id) } : null} value={copyText} defaultValue={copyText} readOnly={readOnly} />
            <div className="snapup_emebed_action position-absolute" style={{ bottom: 5, right: 7 }}>
                <OverlayTrigger placement="top" overlay={<Tooltip>{!codeCopied ? id == 'smsLink' ? 'Copy Message' : 'Copy Code' : id == 'smsLink' ? 'Message Copied' : 'Code Copied'}</Tooltip>}>
                    <Button variant='link' className="me-2 text-gray-700 snapUp_embed_copy" onClick={() => { this.codeCopy(id) }}>
                        {!codeCopied ?
                            <FontAwesomeIcon type="solid" icon={'copy'} />
                            :
                            <FontAwesomeIcon type="solid" icon={'check'} iconClass={'text-success'} />
                        }
                    </Button>
                </OverlayTrigger>
                <OverlayTrigger placement="top" overlay={<Tooltip>Share Code</Tooltip>}>
                    <Button variant='link' className="GetShareCode text-gray-700 snapUp_geneate_code" type="button" onClick={()=>onClickToOpen()}>
                        <FontAwesomeIcon type="solid" icon={'envelope'} iconClass="me-0" />
                    </Button>
                </OverlayTrigger>
            </div>
        </div>

    }
}

export default CodeShareCopy;