import React, { memo, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { debounce } from 'lodash';
import { Form } from 'react-bootstrap';
import HtmlParser from 'react-html-parser';
import { SET_FEED_OBJECT } from '../../../../../../../actions/addFeedActions';

const checkboxesByNetwork = {
    18: [
        { className: "hashtagCaption", id: "feedscreatehashtagCaption", label: "Get posts containing hashtag only in caption.", key: "hashtagCaption", filter: [26] },
        { className: "hashtagIGTV", id: "feedscreatehashtagIGTV", label: "Exclude Videos.", key: "hashtagIGTV", filter: [26, 23] },
        { className: "onlyReels", id: "feedscreateonlyReels", label: `Only Reels. <span class="badge badge-primary ms-1 m-0 py-1">New</span>`, key: "onlyReels", filter: [26, 23, 24, 66, 1] },
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ],
    28: [
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ],
    1: [
        { className: "FeedMultiplePhoto", id: "FeedMultiplePhoto", label: "Add Multiple Photos.", key: "multiplePhoto", filter: [2, 1, 7, 3, 4, 5] },
        { className: "FeedExcludeRetweet", id: "FeedExcludeRetweet", label: "Exclude Retweets.", key: "excludeRetweet", filter: [2, 1, 7, 3, 4, 5] },
        { className: "FeedExcludeReplies", id: "FeedExcludeReplies", label: "Exclude Replies", key: "excludeReplies", filter: [2, 1, 7, 4, 5] },
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ],
    3: [
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ],
    5: [
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ],
    6: [
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ],
    7: [
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ],
    8: [
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ],
    23: [
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ],
    4: [
        { className: "hideforBSearch", id: "feedscreatemoderation", label: "Manually approve posts before making them public?", key: "moderation", filter: [] }
    ]
};

const CheckBoxList = ({ filterData }) => {
    const { networkId } = useParams();
    const dispatch = useDispatch();

    const onInputChange = useCallback(
        debounce((key, value) => {
            dispatch(SET_FEED_OBJECT({ [key]: value }));
        }, 500),
        [dispatch]
    );

    const checkboxList = checkboxesByNetwork[networkId] || [];

    return (
        <>
            {checkboxList.map(data => {
                if (data.filter.includes(filterData.filter) || data.filter.length === 0) {
                    return (
                        <div key={data.id} className={`${data.className} mb-lg-3 mb-2`}>
                            <Form.Check
                                type="checkbox"
                                id={`check-inline-${data.key}`}
                                onChange={e => onInputChange(data.key, e.target.checked)}
                                label={HtmlParser(data.label)}
                                inline
                            />
                        </div>
                    );
                }
                return null;
            })}
        </>
    );
};

export default memo(CheckBoxList);
