import React, { useEffect, useState } from 'react'
import AddAccountBlock from '../../../AddFeed/component/AddAccountBlock';
import { Dropdown, Image, Button, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { FontAwesomeIcon, baseUrl } from '../../../../../../../Assets/AssetsHelper';
import { GET_CHILD_ACCOUNTS } from '../../utils';
import { useParams } from 'react-router-dom';
import { GET_NETWORK_DATA_BY_ID, showAddAccounntAlert, GET_FIRST_LETTER, isPersInstaAcc } from '../../../../../../../utils';
import { useDispatch, useSelector } from 'react-redux';
import { SET_SELECTED_ACCOUNT } from '../../../../../../../actions/addFeedActions';
import { addAccountFromId } from '../../../../../../../utils/addAccount';
import { FETCH_CONNECTED_ACCOUNTS } from '../../../../../../../actions/moderationActions';

const TabAccountList = ({ accounts, activeTab, filterData }) => {

    const { networkId } = useParams()
    const dispatch = useDispatch()
    const { selectedChildAccount, feedObj } = useSelector(state => state.addFeedReducer)
    const [childAccount, setChildAccount] = useState(null)

    useEffect(() => {
        setChildAccount(GET_CHILD_ACCOUNTS(accounts, networkId, feedObj.filter))
    }, [accounts, networkId, feedObj.filter])


    useEffect(() => { if (childAccount) onSelectAccount(childAccount[0]) }, [childAccount])


    const onSelectAccount = (account) => {
        dispatch(SET_SELECTED_ACCOUNT(account, true))
    }

    const addAccount = () => {
        const network = GET_NETWORK_DATA_BY_ID(networkId);
        localStorage.setItem("connectionType", 'addFeed');
        if (network.id == 18 && isPersInstaAcc(parseInt(activeTab))) {
            showAddAccounntAlert({ ...network, id: 2, networkId: 2 }, false, false);
        }
        else showAddAccounntAlert(network, false, false);
    }

    return (

        <div className={`${filterData.filter == 3 ? "col-md-6" : "col-md-12"}`}>
            {accounts && accounts.length ? <Dropdown className="w-100 connected_acc__">
                <Dropdown.Toggle data-id="connect_btn" variant="secondary" className="d-flex align-items-center w-100 justify-content-between p-2">
                    <div className="d-flex align-items-center flex-grow-1">
                        <div className="symbol symbol-30 rounded-circle flex-shrink-0 me-2 overflow-hidden">
                            {selectedChildAccount && selectedChildAccount.ConnectedAccounts && selectedChildAccount.ConnectedAccounts.profilePicture ?
                                <Image src={selectedChildAccount?.ConnectedAccounts?.profilePicture} height="44" width="44" alt={selectedChildAccount && selectedChildAccount.ConnectedAccounts && selectedChildAccount.ConnectedAccounts.profilePicture} fluid onError={event => event.target.src = baseUrl(`media/images/error/no-profile.jpg`)} />
                                :
                                <span className="symbol-label fs-8">{GET_FIRST_LETTER(selectedChildAccount?.ConnectedAccounts?.value2)}</span>
                            }
                        </div>
                        <div className="overflow-hidden w-100 text-start d-flex flex-column" style={{ maxWidth: 300 }}>
                            <p className=" fw-semibold mb-0 text-ellipsis fs-8">{selectedChildAccount?.ConnectedAccounts.accountName ? selectedChildAccount?.ConnectedAccounts.accountName : selectedChildAccount?.ConnectedAccounts.value2 ? selectedChildAccount.ConnectedAccounts.value2 : 'Add Another Account'}</p>
                        </div>
                    </div>
                </Dropdown.Toggle>
                <Dropdown.Menu className="w-100">
                    <p className='text-muted fw-medium px-3 my-2 fs-8'>Select Account :</p>
                    {childAccount && childAccount.length ? childAccount.map(acc => (
                        <Dropdown.Item data-item="connected" eventKey={acc.ConnectedAccounts.id} key={acc.ConnectedAccounts.id} onClick={() => onSelectAccount(acc)} className={`mb-2`}>
                            <div className="d-flex align-items-center">
                                <div className="symbol symbol-30 rounded-circle flex-shrink-0 me-2 overflow-hidden">
                                    {acc.ConnectedAccounts.profilePicture ?
                                        <Image src={acc.ConnectedAccounts.profilePicture} height="44" width="44" alt={selectedChildAccount?.ConnectedAccounts?.profilePicture} fluid onError={event => event.target.src = baseUrl(`media/images/error/no-profile.jpg`)} />
                                        :
                                        <span className="symbol-label fs-8">{GET_FIRST_LETTER(acc.ConnectedAccounts.accountName ? acc.ConnectedAccounts.accountName : acc.ConnectedAccounts.value2)}</span>
                                    }
                                </div>
                                <div className="overflow-hidden w-100 text-start d-flex flex-column" style={{ maxWidth: 300 }}>
                                    <p className=" fw-semibold mb-0 text-ellipsis fs-8">{acc.ConnectedAccounts.accountName ? acc.ConnectedAccounts.accountName : acc.ConnectedAccounts.value2}</p>
                                </div>
                                <div>
                                </div>
                            </div>
                        </Dropdown.Item>
                    ))
                        : <></>
                    }
                    <Dropdown.Item data-item="connect-new" className="navi-item f-between" onClick={addAccount}>
                        <div className='d-flex align-items-center w-100'>
                            <div className='f-center border p-1 h-34px w-34px border-dashed me-2 rounded-circle border-gray'>
                                <FontAwesomeIcon type="solid" icon={'user'} iconClass={'me-0'} />
                            </div>
                            <div className="fw-bold">Add Another Account</div>
                        </div>
                        <FontAwesomeIcon type="solid" icon={'plus'} />
                    </Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
                :
                <AddAccountBlock network={GET_NETWORK_DATA_BY_ID(networkId)} activeTab={activeTab ? activeTab : 0} />}
        </div>
    )
}

export default TabAccountList