import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Card, Button, Row, Col, Alert, Container } from 'react-bootstrap';
import TabInput from './TabInput'
import { Link, useNavigate, useParams } from 'react-router-dom'
import { CREATE_FEED_ACTION } from '../../../../../../actions/addFeedActions'
import { addAccountFromId } from '../../../../../../utils/addAccount'
import { FETCH_CONNECTED_ACCOUNTS } from '../../../../../../actions/moderationActions'
import { inputValidationByNetwork, getFeedAlertText } from '../utils'
import FeedTitle from './FeedTitle';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import SupportVideo from '../../../../../Layout/Main/CommonComponents/Support/SupportVideo'
import { GET_NETWORK_NAME, OPEN_INTERCOM } from '../../../../../../utils';

const channel = new BroadcastChannel('connect');


const FeedBody = ({ network, networkDetails }) => {
    const [isShowVideo, setIsShowVideo] = useState(false)
    const { wallId, networkId } = useParams()
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const controller = new AbortController();
    const { createFeedLoading, error } = useSelector(state => state.addFeedReducer)
    const { feeds } = useSelector(state => state.manageFeeds)

    const onCreateFeed = () => {
        const isError = dispatch(inputValidationByNetwork(networkId))
        if (!isError) dispatch(CREATE_FEED_ACTION(networkId, navigate))
    }

    const handleMessage = async (event) => {
        try {
            if (event?.data && typeof event.data === "string") {
                const networkId = localStorage.getItem('networkId');
                if (networkId) {
                    const response = JSON.parse(event.data);
                    const { code } = response;
                    const type = localStorage.getItem('connectionType');
                    if ((event.origin.includes("tagshop.ai") || event.origin.includes("localhost"))) {
                        if (code) {
                            if (type == 'addFeed') await addAccountFromId(parseInt(networkId), code, () => dispatch(FETCH_CONNECTED_ACCOUNTS(networkId)))
                        }
                    }
                }
            }
        } catch (error) {
            console.log("--- handleMessage error ---", error);
        }
    };

    useEffect(() => {
        window.addEventListener("message", handleMessage, { signal: controller.signal });
        channel.addEventListener("message", handleMessage);
        checkTour()
    }, [networkId])

    useEffect(() => {
        return () => {
            window.removeEventListener("message", handleMessage, { signal: controller.signal });
            channel.removeEventListener("message", handleMessage);
            controller.abort()
            channel.close()

        }
    }, [])

    const checkTour =  () =>localStorage.getItem('isTour') === 'true' && !feeds?.length && localStorage.getItem('tourStep') === '3' &&
        setTimeout(async () => {
            const { default: appTour } = await import('../../../../../../actions/appTour')
            new appTour().tour.drive(1)
        } , 200);

    const alertMsg = dispatch(getFeedAlertText(networkId))
    return <div className='d-flex flex-column flex-column-fluid full-content'>
        <div className="subheader">
            <Container fluid>
                <div className='f-between'>
                    <h1 className="me-2 h4 my-3">Create {GET_NETWORK_NAME(network ? network.id : '')} Feed</h1>
                </div>
            </Container>
        </div>
        <Container fluid>
            <Row className='f-center' style={{ minHeight: 'calc(100vh - 150px)' }}>
                <Col lg={10} xl={8}>
                    <Card>
                        <Card.Body className='p-lg-8'>
                            <FeedTitle network={network} />
                            <TabInput networkId={networkId} network={network} networkDetails={networkDetails} />
                            {alertMsg ? <Alert variant="warning" className='my-2 border border-warning'>
                                <div className='d-flex'>
                                    <FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-6 lh-sm text-warning me-1'} />
                                    {alertMsg}
                                </div>
                            </Alert> : null}
                        </Card.Body>
                        <Card.Footer className='px-lg-8'>
                            <div className='d-flex justify-content-between flex-wrap'>
                                <p className="my-3">
                                    <span className="d-none d-sm-flex align-items-center text-muted py-1 px-3 rounded-pill badge-light-primary">
                                        Need help in adding feeds?
                                        <Button variant='link' className='f-center ms-2 d-flex' onClick={event => setIsShowVideo(!isShowVideo)}>
                                            <FontAwesomeIcon type={'solid'} icon={'play'} iconClass='me-1' />
                                            Watch Video
                                        </Button>
                                        <span className='mx-1'>Or</span>
                                        <Button variant='link' className='f-center ms-1 d-flex' onClick={OPEN_INTERCOM}>
                                            <FontAwesomeIcon type={'solid'} icon={'comments'} iconClass='me-1' />
                                            Connect to Support
                                        </Button>
                                    </span>
                                </p>
                                <div className='d-flex justify-content-end align-items-center p-2'>
                                    <Link to={`/content/addfeed/${wallId}`} className="btn btn-secondary" title='back'>Back</Link>
                                    <Button id="create_feed" className={`ms-3 ${createFeedLoading ? 'spinner' : ''} `} onClick={onCreateFeed}>Create Feed</Button>
                                </div>
                            </div>
                        </Card.Footer>
                    </Card>
                </Col>
            </Row>
        </Container>
        <SupportVideo show={isShowVideo} link={'https://player.vimeo.com/video/907703540?autoplay=1&loop=1&autopause=0'} onClose={event => setIsShowVideo(!isShowVideo)} />
    </div>
}

export default FeedBody