import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import { Dropdown, Image } from 'react-bootstrap'
import { SET_AUTOCOMPLETE_HANDLE, SET_FEED_OBJECT, SET_SELECTED_HANDLE } from '../../../../../../actions/addFeedActions'

const AutoCompleteBlock = ({ filterData, filter, setNetworkText }) => {
    const { networkId } = useParams()
    const dispatch = useDispatch()
    const [showHandle, setshowHandle] = useState(false)
    const handles = useSelector(state => state.addFeedReducer.autoCompleteHandle)

    useEffect(() => {
        if (handles && handles.length) setshowHandle(true)
        else setshowHandle(false)
    }, [handles])


    const onSelectHandle = (item) => {
        var value;
        var feedObj;
        const inputField = document.querySelector(`#${filterData.feedtype.replace(" ", "-")}-text`)
        dispatch(SET_SELECTED_HANDLE(item))

        // -----------for youtube autocomplete--------
        if (networkId == 7) {
            feedObj = { feedText: item.snippet.title, channelId: filterData.filter == 11 ? item.id.playlistId : filterData.feedtype == "Shorts" ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId, };
            value = `https://www.youtube.com/${filterData.feedtype == "Play List" ? "playlist?list=" : "channel/"}${filterData.feedtype == "Play List" ? item.id.playlistId : filterData.feedtype == "Shorts" ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`
            
        }
        // -----------for twitter/facebook autocomplete--------
        else {
            feedObj = { feedText: item.username ? item.username : item.link };
            value = item.username ? item.username : item.link
        }

        setNetworkText(feedObj.feedText)

        dispatch(SET_FEED_OBJECT(feedObj))

        // ---------------set value in input box-----------------------
        inputField.value = value
        dispatch(SET_AUTOCOMPLETE_HANDLE([]))
        setshowHandle(false)
    }

    const getHandleName = (handle) => {
        switch (parseInt(networkId)) {
            // for twitter autocomplete
            case 1:
                return handle.username;
            // for facebook autocomplete
            case 3:
                return handle.name;
            // for youtube autocomplete
            case 7:
                return handle.snippet.channelTitle;
            default:
                return '';
        }
    }

    const getHandleProfilePic = (handle) => {
        // check for youtube autocomplete
        return networkId == 7 ? handle.snippet.thumbnails.default.url : handle.profile_image_url;
    }

    const getHandleSubtitle = (handle) => {
        switch (parseInt(networkId)) {
            // for youtube autocomplete
            case 7:
                return `https://www.youtube.com/${filter === 11 ? "playlist?list=" : "channel/"}${filter === 11 ? handle.id.playlistId : handle.id.channelId}`;
            // for twitter autocomplete
            case 1:
                return `https://twitter.com/${handle.username}`;
            // for facebook autocomplete
            case 3:
                return handle.link;
            default:
                return '';
        }
    }



    return <Dropdown className={`w-100`} autoClose="outside" show={showHandle}>
            {handles && handles.length ? 
                <Dropdown.Menu className="w-100">
                        <div className="dropdown-height">
                            {handles.map(item => (
                                <Dropdown.Item onClick={() => onSelectHandle(item)}>
                                    <div className="d-flex algin-items-center">
                                        {getHandleProfilePic(item) ? <Image className="h-32px w-32px object-center-cover rounded-1 me-2" src={getHandleProfilePic(item)} fluid/> : ''}
                                        <div className="d-flex flex-column justify-content-start">
                                            <p className="fw-bold fs-8 mb-0">{getHandleName(item)}</p>
                                            <span className="fs-10 mb-0">{getHandleSubtitle(item)}</span>
                                        </div>
                                    </div>
                                </Dropdown.Item>
                            ))}
                        </div>
                </Dropdown.Menu>
                : null}
        </Dropdown>
}

export default AutoCompleteBlock