import React, { Component, Suspense } from 'react';
import QRCode from 'react-qr-code'; 
import { WASABI_PATH } from '../../../../../../../actions/api';



const WidgetOnsiteButton = ({ qrCodeStatus, QRButtonText, qrUrl, bgColor, fcColor, fontSize, uniqueCode, onUploadQrChange }) => {
    const QRImage = `${WASABI_PATH}onsiteupload/images/${uniqueCode}.png`
    return <div className="tb_onsite_upload_btn_wrap">
        <div className="tb_onsite_upload_btn">
            <div className='tb_onsite_btn' style={{ backgroundColor: bgColor, color: fcColor }}>
                <div className="tb_onsite_qr_btn_wrap">
                    {qrCodeStatus == 1 ? <div className='tb_onsite_qr_code' style={{ padding: 1 }}>
                        {!uniqueCode ? <QRCode
                            level="L"
                            size={60}
                            value={qrUrl}
                        /> :
                        <img src={QRImage} onError={e => onUploadQrChange(true)} height={60} width={60} alt="QR Code" />
                        }
                    </div> : null}
                    {QRButtonText ?
                        <div className={`tb_onsite_btn_txt`} style={{ fontSize: fontSize }}>
                            {QRButtonText}
                        </div> : ''}
                </div>
            </div>
        </div>
    </div>
}

class QRPreview extends Component {
    state = { error: false, buttonText: "Scan QR or Tap to submit UGC", bgColor: "#000", fcColor: "#fff", fontSize: 8, qrStatus: true };
    onBGColorChange = (value) => {
        this.setState({ bgColor: value })

    }
    onFCColorChange = (value) => {
        this.setState({ fcColor: value })
    }
    onFontSize = event => this.setState({ fontSize: parseInt(event.target.value > 30 ? 30 : event.target.value) })
    onQrStatus = event => {
        this.setState({ qrStatus: event.target.checked })
    }
    onChangeText = event => {
        this.setState({ buttonText: event.target.value })
    }
    render() {
        const { error,   } = this.state
        const { tinyUrl, buttonText,bgColor,fontSize, qrStatus , fcColor, onClickToOpen, uniqueCode, onUploadQrChange } = this.props

        return <div className="f-center bg-light w-100 snapUpContainer">
                        <div className="rounded-1 p-4 w-100 snapup_email_template_prev w-100 border" style={{ maxWidth: 350 }}>
                            <div className="f-center py-3 mx-auto justify-content-between">
                                <div className="bg-gray-200 rounded-4 h-8px mb-2 me-3 w-60px"></div>
                                <div className="f-between w-100" style={{ maxWidth: 150 }}>
                                    <div className="w-75 bg-gray-200 rounded-4 h-8px mb-2 ms-2"></div>
                                    <div className="w-75 bg-gray-200 rounded-4 h-8px mb-2 ms-2"></div>
                                    <div className="w-75 bg-gray-200 rounded-4 h-8px mb-2 ms-2"></div>
                                    <div className="w-75 bg-gray-200 rounded-4 h-8px mb-2 ms-2"></div>
                                </div>
                            </div>
                            <div className="d-flex align-items-start mb-3">
                                <div className="w-100">
                                    <div className="w-100 bg-gray-200 rounded-4 h-8px mb-2"></div>
                                    <div className="w-75 bg-gray-200 rounded-4 h-8px mb-2"></div>
                                </div>
                                <div className="f-center flex-column w-100">
                                    <div className="w-100 bg-gray-200 h-70px" style={{ maxWidth: 130 }}></div>
                                </div>
                            </div>
                            <div className="f-center flex-column mx-auto">
                                <div className="w-25 bg-gray-200 rounded-4 h-8px mb-2 ms-2"></div>
                                <div className="w-50 bg-gray-200 rounded-4 h-8px mb-2 ms-2"></div>
                            </div>
                            <div className="w-100" style={{ transform: 'scale(0.83)' }}>
                                <WidgetOnsiteButton qrCodeStatus={qrStatus} onUploadQrChange={onUploadQrChange} QRButtonText={buttonText} qrUrl={tinyUrl} bgColor={bgColor} uniqueCode={uniqueCode} fcColor={fcColor} fontSize={fontSize} />
                            </div>
                            <div className="d-flex mb-3">
                                <div className="h-100px w-100 bg-gray-200 mx-1 rounded-1"></div>
                                <div className="h-100px w-100 bg-gray-200 mx-1 rounded-1"></div>
                                <div className="h-100px w-100 bg-gray-200 mx-1 rounded-1"></div>
                            </div>
                            <div className="d-flex mb-3">
                                <div className="h-100px w-100 bg-gray-200 mx-1 rounded-1"></div>
                                <div className="h-100px w-100 bg-gray-200 mx-1 rounded-1"></div>
                                <div className="h-100px w-100 bg-gray-200 mx-1 rounded-1"></div>
                            </div>
                            <div className="f-center flex-column mb-3 mx-auto">
                                <div className="w-50 bg-gray-200 rounded-4 h-8px mb-2 ms-2"></div>
                                <div className="w-25 bg-gray-200 rounded-4 h-8px mb-2 ms-2"></div>
                            </div>
                        </div>
                    </div>
    }
}

export default QRPreview;