import { Nav, Tab, Card, Container, Button, Form, Image } from 'react-bootstrap';
import ColorPicker from '../../../../CommonComponents/ColorPicker';
import AttachImage from '../../../../CommonComponents/Uploads/AttachImage';


const Aside = ({onCheckHandler, onChangeHandler, onClickTouploadLogo, changeColor, onClickTouploadImage,  onSiteEnable, title,subtitleError, subTitle,  logoImage, selectedColor, backgroundImage, btnTextError, btnText, msg, error, msgError, isReview, snapLayout, moderation, onChangeModeration }) => {

    return (<>
        <Form.Group className='mb-3'>
            <Form.Check type="switch" id="enable_su" className="f-between" label="Enable" name="onSiteEnable" checked={isReview ? snapLayout==1 &&onSiteEnable : snapLayout == 2 && onSiteEnable} onChange={(e) => onCheckHandler(e)} reverse />
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_title">Title <span className="text-danger">*</span></Form.Label>
            <Form.Control id="su_title" name="title" className={error ? 'is-invalid' : ''} placeholder="Enter Title..." value={title} onChange={(e) => onChangeHandler(e)} />
            {error ? <span className="invalid-feedback">This field is required</span> : null}
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_sub_title">Sub Title</Form.Label>
            <Form.Control as="textarea" name="subTitle" id="su_sub_title" className={subtitleError ? 'is-invalid' : ''} placeholder="Enter Sub Title..." value={subTitle} onChange={(e) => onChangeHandler(e)} />
            {subtitleError ? <span className="invalid-feedback">This field is required</span> : null}
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_logo">Logo</Form.Label>
            <AttachImage id="su_logo" imageStyle={{ paddingBottom: '50%' }} backgroundImage={logoImage} onClickTouploadImage={onClickTouploadLogo} />
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="backgroundColor">Background Color</Form.Label>
            <ColorPicker id="backgroundColor" defaultColor={selectedColor} changeColor={changeColor} />
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_bg_">Background Image</Form.Label>
            <AttachImage id="su_bg_" imageStyle={{ paddingBottom: '66%' }} backgroundImage={backgroundImage} onClickTouploadImage={onClickTouploadImage} />
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_btn_title">Submit Button Text</Form.Label>
            <Form.Control id="su_btn_title" name="btnText" className={btnTextError ? 'is-invalid' : ''} placeholder="Enter Button Text..." value={btnText} onChange={(e) => onChangeHandler(e)} />
            {btnTextError ? <span className="invalid-feedback">This field is required</span> : null}
        </Form.Group>
        <Form.Group className='mb-4'>
            <Form.Label className="mb-2" htmlFor="ty_msg_">Thank You Message</Form.Label>
            <Form.Control as="textarea" name="msg" id="ty_msg_" className={`${msgError ? 'is-invalid' : ''} textarea-min-row-5`} row={5} value={msg} onChange={(e) => onChangeHandler(e)} />
            {msgError ? <span className="invalid-feedback">This field is required</span> : null}
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Check label="Manually approve posts before making them public." type="checkbox" name='moderation' id='moderation' inline checked={moderation} onChange={onChangeModeration}/>
            </Form.Group>
        </>
    )
}

export default Aside