import { FontAwesomeIcon } from "../../../../../../../Assets/AssetsHelper";
import { generateRandomArray } from "../../../../../../../utils";

const Preview = ({ title, subTitle, backgroundImage, selectedColor, btnText, logoImage, isReview }) => {
    
        return (<div className="f-center flex-wrap snapUpContainer" style={{ backgroundColor: selectedColor, backgroundImage: `url(${backgroundImage})` }}>
                <div className="onsite_preview_inner w-100">
                    <div className="t_modal_wrap">
                        <div className="t_modal_container">
                            <div className="t_modal_content">
                                {logoImage ? <div className="t_m_logo_w">
                                    <img loading="lazy" src={logoImage} className="t_m_logo" width="400" height="50" alt="logo" />
                                </div> : null}
                                <div className="t_m_close_btn fa fa-xmark">
                                    <div></div>
                                </div>
                                {title ? <div className="t_m_title">{title}</div> : null}
                                {subTitle ? <div className="t_m_subtitle">{subTitle}</div> : null}
                                {isReview ? <div className="t_f_group">
                                    <div className="t_label_ t_label_center">Rate the Product <div className="t_t_danger">*</div></div>
                                    <div className="t_rate_wrap">
                                    {generateRandomArray(5).map((number, index) => <div key={index} className={`t_star_ico_wrap ${number <= 4 ? 't_h_active_' :''}`}>
                                        <div className="t_star_ico w-44px h-44px">
                                            <FontAwesomeIcon icon={'star'} />
                                        </div>
                                    </div>)}
                                    </div>
                                </div> : null}
                                {isReview ? <div className="t_f_group">
                                    <div className="t_label_">
                                    Review Title 
                                    <div className="t_t_danger">*</div>
                                    </div>
                                    <input type="text" className="t_from_input " placeholder="Enter review title" value=""/>
                                </div> : null}
                                <div className="t_f_group">
                                    <div className="t_label_">
                                    {isReview ? 'Review' : 'Caption'}  
                                    <div className="t_t_danger">*</div>
                                    </div>
                                    <textarea className="t_from_textarea" placeholder={`Write your ${isReview ? "review" : "caption"} here`} readOnly></textarea>
                                </div>
                                <div className="t_f_group">
                                    <div className="t_label_">Picture/Video (optional)</div>
                                    <div className="t_f_upload">
                                        <div className="t_m_upload t_m_dd">
                                            <div className="t_m_up_wrp">
                                                <div className="uppy-Container">
                                                    <div className="uppy-Root" dir="ltr">
                                                    <div className="uppy-u-reset">
                                                        <div className="uppy-DragDrop-inner">
                                                                <div className="uppy-DragDrop-label">Drag a video / image to upload (optional)</div>
                                                                <span className="uppy-DragDrop-note text-gray-800">Video Size : Max 100MB | Photo Size : Max 15MB | Max 20 Posts</span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="t_row_">
                                    <div className="t_col_">
                                        <div className="t_f_group">
                                            <div className="t_label_">
                                                Name  
                                                <div className="t_t_danger">*</div>
                                            </div>
                                            <input type="text" className="t_from_input " placeholder="Enter Your name" value=""/>
                                        </div>
                                    </div>
                                    <div className="t_col_">
                                    <div className="t_f_group">
                                        <div className="t_label_">
                                            Email  
                                            <div className="t_t_danger">*</div>
                                        </div>
                                        <input type="text" className="t_from_input " placeholder="Enter you email" value=""/>
                                    </div>
                                    </div>
                                </div>
                                <div className="t_f_group">
                                    <div className="t_f_check_inline"><input type="checkbox" id="t_accept" className="t_from_check" value="accept" checked=""/><label className="t_check_label_" for="t_accept">By clicking on Submit, you are agreeing to our <a href="https://tagshop.ai/snapup-content-rights/" className="t_tc_link" target="_blank" title='terms and conditions'>terms and conditions</a> *</label></div>
                                </div>
                                <div className="t_f_group t_text_center">
                                    <div className="t_f_p_btn ">{btnText ? btnText : 'Submit'}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                </div>
        );
}


export default Preview