import { Nav, Tab, Card, Container, Button, Form, Image } from 'react-bootstrap';
import ColorPicker from '../../../../CommonComponents/ColorPicker';
import { CLOUDPATH_TAGSHOP } from '../../../../../../../actions/api';
import RangeInput from '../../../../CommonComponents/RangeInput';




const QRAside = ({ publishOption, onClickToOpen, onChangeText, onFontSize, onQrStatus, uniqueCode, bgColor,onBgColorChange, onFcColorChange, fcColor, fontSize, qrStatus, tinyUrl, buttonText, error }) => {

    return (
        <>
                <Form.Group className='mb-3'>
                    <Form.Label className="mb-2" htmlFor="btn_text_">Button Text</Form.Label>
                    <Form.Control id="btn_text_" name="title" className={error ? 'is-invalid' : ''} placeholder="Enter Button Title..." value={buttonText} onChange={onChangeText} />
                    {error ? <span className="invalid-feedback">This field is required</span> : null}
                </Form.Group>

                <Form.Group className='mb-3'>
                    <Form.Label className="mb-2" htmlFor="btn_bg_color">Background Color</Form.Label>
                    <ColorPicker id="webBackgroundColor" defaultColor={bgColor} changeColor={onBgColorChange} />
                </Form.Group>

                <Form.Group className='mb-3'>
                    <Form.Label className="mb-2" htmlFor="btn_text_color">Font Color</Form.Label>
                    <ColorPicker id="webFontColor" defaultColor={fcColor} changeColor={onFcColorChange} />
                </Form.Group>

                <Form.Group className='mb-3'>
                    <Form.Label className="mb-2" htmlFor="btn_fs_">Font Size</Form.Label>
                    <RangeInput id="btn_fs_" min={10} max={25} step={1} defaultValue={fontSize} value={fontSize} onChange={e => onFontSize(e)} />
                </Form.Group>

                <Form.Group className='mb-3'>
                    <Form.Check type="switch" id="qr_status" className="f-between" label="Show QR" defaultValue={qrStatus} checked={qrStatus} onChange={onQrStatus} reverse />
                </Form.Group>
        </>
    )
}

export default QRAside