import React from 'react'
import { showAddAccounntAlert, isPersInstaAcc, GET_NETWORK_ICON } from '../../../../../../utils';
import { Button } from 'react-bootstrap';
import { baseUrl } from '../../../../../../Assets/AssetsHelper';

const AddAccountBlock = ({ network, activeTab }) => {
    const addAccount = () => {
        localStorage.setItem("connectionType", 'addFeed');
        showAddAccounntAlert(network.id == 18 ? isPersInstaAcc(parseInt(activeTab)) ? { ...network, id: 2, networkId: 2 } : network : network, false, false)
    }

    return (
        network ? <div className="py-2">
            <Button variant='outline' size='lg' className="align-items-center d-flex signInWithBrand m-auto" onClick={addAccount}>
                <span className="h-40px w-40px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-gray-200">
                    <img src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(network?.id)}.svg`)} height={43} width={43} alt={`${network?.name}`} />
                </span>
                <div className="overflow-hidden signIn_txt px-5"><span className="mb-0 fw-medium text-ellipsis me-2">Connect {network?.name}</span></div>
            </Button>
        </div>
            : <>error</>
    )
}

export default AddAccountBlock