import React, { useState } from "react";
import { InputGroup, Form, Dropdown } from "react-bootstrap";
import { FontAwesomeIcon } from "../../../../../../Assets/AssetsHelper";
import PlacesAutocomplete, { geocodeByAddress, getLatLng } from "react-places-autocomplete";
import { useDispatch } from "react-redux";
import { SET_FEED_OBJECT } from "../../../../../../actions/addFeedActions";
import { useParams } from "react-router-dom";

export default function SearchPlace({filterData, fieldError, activeTab}) {
  const { networkId } = useParams()
  const [address, setAddress] = useState("");
  const [list, setList] = useState(false);
  const dispatch = useDispatch()


  const handleChange = (e) => { setAddress(e); setList(true) };
  const handleSelect = address => {

  };

  const handleSelect1 = (v1, v2, v3, address) => {
    if (networkId == 7) {
      geocodeByAddress(address)
        .then(results => getLatLng(results[0]))
        .then(latLng => {
          dispatch(SET_FEED_OBJECT({ latLng: `${latLng.lat},${latLng.lng}`, feedText: v1 }))
        })
        .catch(error => console.error('Error', error));
    }
    else {
      dispatch(SET_FEED_OBJECT({ feed33: v1, field33placeId: v2, field33placeName: v3 }))
    }
    setAddress(v1);
    setList(false)
  };



  return <InputGroup className="mb-3">
            <InputGroup.Text id={`loc-network-${networkId}`} >
            <FontAwesomeIcon icon={'globe'} />
            </InputGroup.Text>
            <PlacesAutocomplete value={address} onChange={handleChange} onSelect={handleSelect}>
              {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
                <Dropdown show={true}>
                    <Dropdown.Toggle variant="outline-secondary" className={`arrow_disabled border-0 p-0 flex-grow-1 bg-transparent  ${filterData.filter == activeTab && fieldError?.show ? 'is-invalid' : ''}`}>
                      <Form.Control data-input="network" size="lg" className="fw-medium" {...getInputProps({ placeholder: "Type address" })}/>
                    </Dropdown.Toggle>
                    {list && address.length > 0 ?
                    <Dropdown.Menu show={list && address.length > 0 ? true : false} style={{width:'calc(100% - 44px)'}}>
                        {suggestions.map((suggestion, i) => {
                          const style = {
                            backgroundColor: suggestion.active ? "" : ""
                          };
                          return (<Dropdown.Item data-item="place" className="text-ellipsis d-block" {...getSuggestionItemProps(suggestion, { style })} onClick={() => handleSelect1(suggestion.description, suggestion.placeId, suggestion.formattedSuggestion.mainText, suggestion.description)} key={i}> {suggestion.description}</Dropdown.Item>);
                        })}
                    </Dropdown.Menu>
                    : ''}
                </Dropdown>
              )}
            </PlacesAutocomplete>
            {fieldError?.show ? <span id="" className="invalid-feedback">{fieldError?.message || ""}</span> : null}
          </InputGroup>
}


