import React, { Component } from 'react';
import $ from 'jquery';
import { Uppy } from "@uppy/core";
import '@uppy/core/dist/style.min.css';
import '@uppy/dashboard/dist/style.min.css';
import '@uppy/drag-drop/dist/style.min.css';
import '@uppy/status-bar/dist/style.min.css';
import '@uppy/webcam/dist/style.min.css';
import Transloadit from "@uppy/transloadit";
import GoogleDrive from '@uppy/google-drive';
import Url from '@uppy/url';
import { DashboardModal } from '@uppy/react';
import { OverlayTrigger, Tooltip, Image, Button } from 'react-bootstrap';
import { IS_LIVE } from '../../../../../actions/api';
import { TEMP_URL } from '../../../../../actions/constants';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';


const uppyConfig = {
       uppyKey: '0701d3d7f8d94e92a65a9cba388b1d96',
       uppyBucket: 'cloud.tagshop.ai',
       uppyAwsKey: '47ABVORKG52UYE1KB28S',
       uppyAwsSecret: 'pcaTfeokaz28geAAns08H3yaGbOVz6ILgTptu19v',
       companionUrl: IS_LIVE ? 'https://app.taggbox.com' : 'https://test.taggbox.com'
}

export default class AttachImage extends Component {
       constructor(props) {
              super(props)
              this.uppy = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 5000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: ['image/*'] } })
                     .use(Transloadit, {
                            target: '#a',
                            params: {
                                   auth: {
                                          key: uppyConfig.uppyKey
                                   },
                                   steps: {
                                          store: {
                                                 robot: "/wasabi/store",
                                                 bucket_region: "us-west-1",
                                                 bucket: uppyConfig.uppyBucket,
                                                 key: uppyConfig.uppyAwsKey,
                                                 secret: uppyConfig.uppyAwsSecret,
                                                 path: 'tmp/${file.id}.${file.ext}'
                                          }
                                   }
                            },
                            waitForEncoding: true
                     })
                     .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })
                     .use(Url, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })
       }
       state = {
              backgroundModal: false
       }
       componentDidMount() {
              this.uppy.on('transloadit:result', (stepName, result) => {
                     this.props.onClickTouploadImage(`${TEMP_URL}${result.id}.${result.ext}`, () => {
                            setTimeout(() => {
                                   this.uppy.cancelAll()
                                   this.setState({ backgroundModal: false })
                            }, 300)
                     })
              })
       }
       onBackgroundClose = event => {
              this.uppy.cancelAll()
              this.setState({ backgroundModal: false })
       }
       onBackgroundOpen = event => {
              this.setState({ backgroundModal: true }, () => {
                     $(".uppy-Dashboard-close").click(() => {
                            this.uppy.cancelAll()
                            this.setState({ backgroundModal: false })
                     })
              })
       }
       render() {
              const { imageStyle, backgroundImage, id } = this.props
              const { backgroundModal } = this.state
              return <>
                     <div className="d-flex align-items-start justify-content-between flex-column py-1">
                            {!backgroundImage ? <Button id={id} variant='secondary' className="w-100" onClick={() => this.setState({ backgroundModal: true })}><FontAwesomeIcon icon={'circle-plus'} />Attach Image</Button> : null}
                            {backgroundImage ?
                                   <div className="bg-white w-100 rounded-1 position-relative">
                                          <Image id="bg_image" className="object-fit-contain" src={backgroundImage} height={300} width={300} alt="" style={{ maxHeight: 250 }} fulid />
                                          <div className="position-absolute bottom-0 start-0 w-100 f-center">
                                                 <Button variant="icon" size="sm" className="rounded-circle btn-active-primary m-2 box-shadow border-0" onClick={() => this.setState({ backgroundModal: true })}>
                                                        <FontAwesomeIcon type='solid' icon={'pen'} />
                                                 </Button>
                                                 <Button variant="icon" size="sm" className="rounded-circle btn-active-primary m-2 box-shadow border-0" onClick={() => this.props.onClickTouploadImage('', () => { })}>
                                                        <FontAwesomeIcon icon={'xmark'} />
                                                 </Button>
                                          </div>
                                   </div> : null}
                     </div>
                     {
                            backgroundModal ?
                                   <DashboardModal
                                          animateOpenClose={true}
                                          open={backgroundModal}
                                          proudlyDisplayPoweredByUppy={false}
                                          inline={false}
                                          showProgressDetails={true}
                                          height={750}
                                          browserBackButtonClose={true}
                                          showRemoveButtonAfterComplete={false}
                                          uppy={this.uppy}
                                          onRequestClose={this.onBackgroundClose}
                                          closeAfterFinish={true}
                                          plugins={['Url']}
                                   />
                                   : null
                     }
              </>
       }

}