import React, { useEffect, useState } from 'react'
import { Tab, Tabs, Nav } from 'react-bootstrap'
import TabBody from './TabBody'
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import { SET_FEED_OBJECT } from '../../../../../../../actions/addFeedActions'
import { RESET_AUTOCOMPLETE } from '../../utils'
import { isPersInstaAcc } from '../../../../../../../utils'

const TabInput = ({ networkDetails }) => {
    const { networkId } = useParams()
    const dispatch = useDispatch()

    const [activeTab, setActiveTab] = useState(networkDetails?.networkTab ? networkDetails.networkTab[0].filter : null)
    const { personalAccount, connectedAcc } = useSelector(state => state.addFeedReducer)
    const [userAccounts, setUserAccounts] = useState(null)

    useEffect(() => {
        if (isPersInstaAcc(parseInt(activeTab)) && networkId == 18) setUserAccounts(personalAccount)
        else setUserAccounts(connectedAcc)
        if (!activeTab && networkDetails?.networkTab) setActiveTab(networkDetails.networkTab[0].filter)
        dispatch(RESET_AUTOCOMPLETE())
    }, [activeTab, connectedAcc, personalAccount, networkId, networkDetails])

    useEffect(() => { }, [userAccounts])
    useEffect(() => { setActiveTab(() => null) }, [networkId, networkDetails])
    useEffect(() => { if (!activeTab && networkDetails?.networkTab) setActiveTab(networkDetails.networkTab[0].filter) }, [networkDetails])

    //   ------------update filter on tab change---------
    useEffect(() => { dispatch(SET_FEED_OBJECT({ filter: activeTab, feedText: '' })); }, [activeTab, dispatch, networkDetails])

    if (!networkDetails.loading && networkDetails?.networkTab) {
        return <Tab.Container key={`${networkId}_${activeTab}`} activeKey={activeTab} defaultActiveKey={networkDetails.networkTab[0].filter} variant='underline' className="mb-5" onSelect={e => setActiveTab(e)}>
            <Nav variant="underline" className="mb-5 border-bottom">
                {networkDetails.networkTab.map((filter, index) => <Nav.Item key={index}>
                    <Nav.Link eventKey={filter.filter}>{filter.feedName}</Nav.Link>
                </Nav.Item>)}
            </Nav>
            <Tab.Content key={networkId}>
                {networkDetails.networkTab.map((filter, index) => <Tab.Pane eventKey={filter.filter} key={filter.filter}>
                    <TabBody filterData={filter} networkDetails={networkDetails} activeTab={activeTab} accounts={userAccounts} />
                </Tab.Pane>
                )}
            </Tab.Content>
        </Tab.Container>

    }
    return <></>
}

export default TabInput