import React, { useEffect, useState } from 'react'
import CheckBoxList from './CheckBoxList'
import TabInputTab from './TabInputTab'
import { useDispatch, useSelector } from 'react-redux';
import TabAccountList from './TabAccountList';
import TabAccountPicker from '../../../AddFeed/component/TabAccountPicker';
import { SET_SELECTED_ACCOUNT } from '../../../../../../../actions/addFeedActions';
import { useParams } from 'react-router-dom';
import AddAccountBlock from '../../component/AddAccountBlock';
import { GET_NETWORK_DATA_BY_ID } from '../../../../../../../utils';
import TwitterAdvanceTable from '../../../AddFeed/component/twitterAdvanceTable';

const RenderBody = ({ filterData, accounts, activeTab }) => {
    const { networkId } = useParams()

    const filters = [26, 23, 71, 72, 70, 2, 7, 3, 4, 5, 8, 33, 75, 11, 12, 56, 67, 32];

    // ----------check for input field | excluded instagram personal tab with filter id = 1 which is common---------
    if (filters.includes(filterData.filter) || (filterData.filter == 1 && networkId != 18)) {
        return <TabInputTab filterData={filterData} accounts={accounts} activeTab={activeTab} />;
    }

    // ----------for account list --------------------
    else return <div className='mb-4'><TabAccountList accounts={accounts} activeTab={activeTab} filterData={filterData} /></div>
}

const TabBody = ({ filterData, accounts, networkDetails, activeTab }) => {
    const { networkId } = useParams()
    const dispatch = useDispatch()
    const showAccountPicker = () => {
        const filters = [26, 23, 71, 72, 70, 2, 7, 3, 4, 5, 8, 75, 11, 12, 56, 67];
        if (filters.includes(filterData.filter) || (filterData.filter == 1 && networkId != 18)) return true
        return false
    }
    useEffect(() => { }, [accounts])

    useEffect(() => {
        // ---------------set default account------------
        if (accounts?.length) dispatch(SET_SELECTED_ACCOUNT(accounts[0]))

        return () => {
            if (accounts?.length) dispatch(SET_SELECTED_ACCOUNT(accounts[0]))
        }
    }, [accounts, dispatch]);
         return (
        // -----check for authorization in authorization mandatory networks/tabs----------------  
        ((networkDetails.authRequire && !accounts?.length)) && [18, 1, 3, 8].includes(parseInt(networkId)) ?
            <AddAccountBlock network={GET_NETWORK_DATA_BY_ID(networkId)} activeTab={activeTab} />
            :
            <>
                {filterData.filter == 1 && networkId == 3
                    ?
                    <></>
                    :
                    <>
                        {/* ----------render input/account list for tab-------- */}
                        <RenderBody accounts={accounts} filterData={filterData} activeTab={activeTab} />
                        {filterData.filter == 5 && networkId == 1 ?
                            <TwitterAdvanceTable />
                            :
                            <></>
                        }
                    </>
                }
                <div className='mb-4'>
                    <CheckBoxList filterData={filterData} />
                </div>
                {showAccountPicker() ? <div className='mb-4'><TabAccountPicker accounts={accounts} /></div> : <></>}
            </>
    )
}

export default TabBody