import React, { useEffect } from 'react'
import { Dropdown, Image, Button, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { useDispatch, useSelector } from 'react-redux';
import { ACCOUNT_DISCONNECT, SET_SELECTED_ACCOUNT } from '../../../../../../actions/addFeedActions';
import Swal from 'sweetalert2';
import { useParams } from 'react-router-dom';
import { GET_NETWORK_DATA_BY_ID, showAddAccounntAlert, GET_FIRST_LETTER } from '../../../../../../utils';
import { baseUrl } from '../../../../../../Assets/AssetsHelper';


const TabAccountPicker = ({ accounts }) => {
    const { networkId } = useParams()
    const dispatch = useDispatch()
    const selectedAccount = useSelector(state => state.addFeedReducer.selectedAccount)

    useEffect(() => {
        if (accounts && accounts.length) onSelectAccount(accounts[0])
    }, [])

    const onSelectAccount = (account) => {
        dispatch(SET_SELECTED_ACCOUNT(account))
    }

    const disconnectAccount = (account) => {
        const obj = {
            "account": account.ConnectedAccounts.id,
            "networkId": account.ConnectedAccounts.networkId
        }

        dispatch(ACCOUNT_DISCONNECT(obj))

    }

    const deleteAccount = (account) => {
        Swal.fire({
            title: '<strong>Confirm</strong>',
            icon: 'warning',
            text:
                'Do you want to disconnect account?',
            showCloseButton: true,
            showCancelButton: true,
            focusConfirm: true,
            confirmButtonText:
                'Yes',
            confirmButtonAriaLabel: 'Yes',
            cancelButtonAriaLabel: 'Thumbs down'
        }).then((result) => {
            if (result.value) {
                disconnectAccount(account)
            }
            else if (result.dismiss === "cancel") { }
        })
    }

    const addAccount = () => {
        const network = GET_NETWORK_DATA_BY_ID(networkId);
        localStorage.setItem("connectionType", 'addFeed');
        showAddAccounntAlert(network, false, false);
    }

    return (
        accounts && accounts.length ?
            <Dropdown className="w-100 connected_acc__">
                <Dropdown.Toggle data-id="connect_btn" variant="secondary" className="d-flex align-items-center w-100 justify-content-between p-2">
                    <div className="d-flex align-items-center flex-grow-1">
                        <div className="symbol symbol-30 rounded-circle flex-shrink-0 me-2 overflow-hidden">
                            {selectedAccount?.ConnectedAccounts?.profilePicture ?
                                <Image src={selectedAccount.ConnectedAccounts.profilePicture} height="44" width="44" alt={selectedAccount.ConnectedAccounts.accountName} fluid onError={event => event.target.src = baseUrl(`media/images/error/no-profile.jpg`)} />
                                :
                                <span className="symbol-label fs-8">{GET_FIRST_LETTER(selectedAccount?.ConnectedAccounts?.accountName)}</span>
                            }
                        </div>
                        <div className="overflow-hidden w-100 text-start d-flex flex-column" style={{ maxWidth: 300 }}>
                            <p className=" fw-semibold mb-0 text-ellipsis fs-8">{selectedAccount ? selectedAccount.ConnectedAccounts.accountName : 'Add Another Account'}</p>
                        </div>
                    </div>
                </Dropdown.Toggle>
                <Dropdown.Menu className="w-100">
                    <p className='text-muted fw-medium px-3 my-2 fs-8'>Select Account :</p>
                    {accounts && accounts.length ? accounts.map(acc => (
                        <div className='position-relative'>
                            <Dropdown.Item data-item="connected" eventKey={acc.ConnectedAccounts.accountId} key={acc.ConnectedAccounts.accountId} onClick={() => onSelectAccount(acc)} className={`mb-2`}>
                                <div className="d-flex align-items-center">
                                    <div className="symbol symbol-30 rounded-circle flex-shrink-0 me-2 overflow-hidden">
                                        {acc.ConnectedAccounts.profilePicture ?
                                            <Image src={selectedAccount?.ConnectedAccounts?.profilePicture} height="44" width="44" alt={acc?.ConnectedAccounts?.profilePicture} fluid onError={event => event.target.src = baseUrl(`media/images/error/no-profile.jpg`)} />
                                            :
                                            <span className="symbol-label fs-8">{GET_FIRST_LETTER(selectedAccount?.ConnectedAccounts?.accountName)}</span>
                                        }
                                    </div>
                                    <div className="overflow-hidden w-100 text-start d-flex flex-column" style={{ maxWidth: 300 }}>
                                        <p className=" fw-semibold mb-0 text-ellipsis fs-8">{acc.ConnectedAccounts.accountName}</p>
                                    </div>
                                    <div>
                                    </div>
                                </div>
                            </Dropdown.Item>
                            <div className='dropdown-action pe-2'>
                                <OverlayTrigger placement="top" overlay={<Tooltip>Account Disconnect</Tooltip>}>
                                    <Button data-button="disconnect" variant='icon' size='sm' className='btn-active-primary' aria-label="delete" onClick={() => deleteAccount(acc)}>
                                        <FontAwesomeIcon type="solid" icon={'trash-can'} />
                                    </Button>
                                </OverlayTrigger>
                            </div>
                        </div>
                    ))
                        : <></>
                    }
                    <Dropdown.Item data-item="connect-new" className="navi-item f-between" onClick={addAccount}>
                        <div className='d-flex align-items-center w-100'>
                            <div className='f-center border p-1 h-34px w-34px border-dashed me-2 rounded-circle border-gray'>
                                <FontAwesomeIcon type="solid" icon={'user'} iconClass={'me-0'} />
                            </div>
                            <div className="fw-bold">Add Another Account</div>
                        </div>
                        <FontAwesomeIcon type="solid" icon={'plus'} />
                    </Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
            :
            <></>
    )
}

export default TabAccountPicker